*, :before, :after {
  box-sizing: border-box;
}

h1, h2, h3, h4, h5, h6, body, ul, p, pre {
  margin: 0;
}

body {
  padding: 0;
}

ul, ol {
  padding: 0;
  list-style: none;
}

button {
  color: currentColor;
  font-size: inherit;
  font-weight: inherit;
  font-family: inherit;
  font-style: inherit;
  line-height: inherit;
  word-spacing: inherit;
  cursor: pointer;
  background-color: #0000;
  border: none;
}

img {
  display: block;
}

a {
  color: currentColor;
  text-decoration: none;
}

input {
  border: none;
}

html {
  --color-1: #171822;
  --color-2: #fafbfb;
  --color-3: #1c1d29;
  --color-4: #101118;
  --color-5: #c0c2d2;
  --color-6: #1c204d;
  --color-7: #464646;
  --g-b-r: 5px;
  --g-t: .3s ease;
}

body {
  background-color: var(--color-1);
  color: var(--color-2);
  font-family: Arial, sans-serif;
  font-size: 1rem;
  line-height: 1.4;
}

.container {
  flex-direction: column;
  gap: 20px;
  width: 100%;
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  display: flex;
}

.sub-container {
  flex-direction: column;
  gap: 20px;
  display: flex;
}

.app-input {
  border-radius: var(--g-b-r);
  color: #acb1bb;
  background-color: #12131a;
  border: none;
  width: 100%;
  padding: 16px;
  font-size: 1.2rem;
  box-shadow: 0 2px 4px #0000001a;
}

.app-input::placeholder {
  color: #acb1bb;
}

.app-list {
  flex-direction: column;
  gap: 10px;
  display: flex;
}

.app-button {
  background-color: var(--color-3);
  border-radius: var(--g-b-r);
  width: 100%;
  transition: background-color var(--g-t);
  color: var(--color-5);
  padding: 10px 15px;
}

.app-button:hover {
  background-color: var(--color-4);
}

.app-button:disabled {
  background-color: var(--color-4);
  cursor: not-allowed;
}

.container .title {
  margin-top: 50px;
  margin-bottom: 10px;
  font-size: 24px;
  font-weight: bold;
  position: relative;
}

.title:after {
  content: "";
  background-color: #ffffff4d;
  width: 100%;
  height: 2px;
  position: absolute;
  bottom: -5px;
  left: 0;
}

.flex-container {
  --flex-items: 1;
  --flex-items-gap: 25px;
  gap: var(--flex-items-gap);
  flex-wrap: wrap;
  display: flex;
}

.flex-container > li {
  flex-basis: calc((100% - (var(--flex-items)  - 1) * var(--flex-items-gap)) / var(--flex-items));
}

::-webkit-scrollbar {
  width: 5px;
}

::-webkit-scrollbar-track {
  background: none;
}

::-webkit-scrollbar-thumb {
  background: var(--color-2);
  border-radius: 2px;
}

::-webkit-scrollbar-thumb:hover {
  background: #555;
}

.modal {
  z-index: 100;
  width: 100%;
  height: 100%;
  transition: opacity var(--g-t);
  opacity: 0;
  pointer-events: none;
  background-color: #00000080;
  justify-content: center;
  align-items: center;
  padding: 20px;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.modal.active {
  opacity: 1;
  pointer-events: all;
}

.modal .modal-container {
  background-color: var(--color-1);
  border-radius: 4px;
  box-shadow: 0 2px 4px #0000001a;
}

.modal-title {
  justify-content: space-between;
  display: flex;
}

.modal-word:first-letter {
  text-transform: uppercase;
  width: 100%;
}

.modal-info {
  font-size: .8em;
}

.noun {
  color: #d21b1b;
}

.verb {
  color: #e15200;
}

.adjective {
  color: #e0cd00;
}

.adverb {
  color: #00d115;
}

.verb_phrase {
  color: #394cfc;
}

.phrasal_verb {
  color: #87ffd9;
}

.noun_phrase {
  color: #b11dd6;
}

.adjective_phrase {
  color: #d07ce6;
}

.phrase {
  color: #b80075;
}

.phrasal {
  color: #3646dd;
}

.proverb {
  color: #51bb00;
}

.conjunction {
  color: #b800b8;
}

.adverbial_phrase {
  color: #d5a8d5;
}

.preposition {
  color: #00b2bb;
}

.favorite {
  color: #e17979;
}

.interjection {
  color: #51bb00;
}

.dictionary-list {
  border-radius: var(--g-b-r);
  flex-direction: column;
  gap: 5px;
  max-height: 400px;
  display: flex;
  overflow-y: auto;
}

.word-button {
  -webkit-user-select: none;
  user-select: none;
  text-align: left;
  display: flex;
}

.word-button .index {
  margin-right: 15px;
}

.word-button .expression {
  -webkit-user-select: all;
  user-select: all;
  color: var(--color-2);
}

.word-button .expression:first-letter {
  text-transform: capitalize;
  font-weight: 600;
}

.word-button .type {
  margin-left: 10px;
  display: none;
}

@media screen and (width >= 500px) {
  .word-button .type {
    display: block;
  }
}

.word-button .id {
  margin-left: auto;
  margin-right: 0;
}

.filter-list {
  --flex-items: 1;
  --flex-items-gap: 5px;
  flex-direction: row;
}

@media screen and (width >= 300px) {
  .filter-list {
    --flex-items: 2;
  }
}

@media screen and (width >= 450px) {
  .filter-list {
    --flex-items: 3;
  }
}

.filter-list input {
  visibility: hidden;
  width: 0;
  height: 0;
  position: absolute;
}

.filter-list label {
  cursor: pointer;
  background-color: var(--color-3);
  border-radius: var(--g-b-r);
  text-align: center;
  width: 100%;
  height: 100%;
  transition: color var(--g-t);
  justify-content: center;
  align-items: center;
  padding: 10px;
  display: flex;
}

.filter-list input:not(:checked) ~ label {
  color: var(--color-7);
}

.filter-list-des-box {
  overflow: hidden;
}

.filter-list-des-box > div {
  transition: margin-top var(--g-t);
}

.filter-list-des-box.hidden > div {
  margin-top: -100%;
}

.filter-open-close-button {
  text-align: left;
  align-items: center;
  gap: 10px;
  display: flex;
}

.filter-open-close-button > svg {
  transition: transform var(--g-t);
}

.filter-open-close-button.active > svg {
  transform: rotate(180deg);
}

.train-writing-text-textarea {
  border-radius: var(--g-b-r);
  resize: none;
  background-color: var(--color-1);
  width: 100%;
  height: 300px;
  color: var(--color-2);
  border: 1px solid var(--color-5);
  outline: none;
  padding: 8px 16px;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 16px;
}

.train-word-input:focus {
  outline: none;
}

.train-grammar-test-generate-button {
  margin-top: 10px;
}

.topic-title {
  font-size: 20px;
  font-weight: 600;
}

.test-list > li > p {
  font-size: 16px;
  font-weight: 600;
}

.test-list > li > ul {
  margin-left: 50px;
  list-style-type: decimal;
}

.lang-switcher-list {
  display: flex;
}

.lang-switcher-list li {
  flex-basis: 100%;
}

.lang-switcher {
  cursor: pointer;
  justify-content: center;
  align-items: center;
  display: flex;
}

.lang-radio-switcher {
  visibility: hidden;
  width: 0;
  height: 0;
  position: absolute;
}

.lang-radio-switcher:checked ~ .lang-switcher {
  background-color: var(--color-6);
}
